.container{
    display: flex;
}

.profileBox{
    width: 400px;
    height: 400px;
    margin-top: 140px;
    margin-left: 100px;
    border-radius: 54px;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
    padding: 20px;
}

.profileBox:hover {
    box-shadow: 0 0 5px #1388a8, 0 0 15px #1388a8, 0 0 20px #1388a8, 0 0 40px #96D7E9, 0 0 60px #31a9d8, 0 0 10px #96D7E9, 0 0 98px #31a9d8;
    background-color: #fff6a9;
}

.profileBox img{
    width: 400px;
    border-radius: 40px;
}

.summaryBox{
    width: 600px;
    margin-top: 80px;
    margin-left: 80px;
    border-radius: 10px;
    padding: 20px;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
}

.summaryBox p{
    font-size: 24px;
    text-align: justify;
    color :#C4C4C4;
    margin: 0px;
}

.about{
    width: 390px;
    display: flex;
    justify-content: center;
    border-radius: 10px;
    padding: 10px;
    margin-top: 140px;
    margin-left: 80px;
    align-items: center;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
}

.about h1{
    font-size: 30px;
    color :aqua;
    margin: 0px;
    text-shadow: 0 0 5px #1388a8, 0 0 15px #1388a8, 0 0 20px #1388a8, 0 0 40px #96D7E9, 0 0 60px #31a9d8, 0 0 10px #96D7E9, 0 0 98px #31a9d8;
    color: #fff6a9;
    letter-spacing: 4px;
}



 